import React, { Component } from "react";


class Login extends Component{

    constructor(){
        super();
    }

    render(){
        return(<div><br/>
                <input type="text" defaultValue="User Name" id="username"/><br/><br/>
                <input type="password" defaultValue="Password" id="password" /><br/><br/>
                <button value="Submit">Login</button>
                </div>
            );
    }

}

export default Login;


