import React, { Component } from "react";
import logo from './logo.svg';
import ReactDOM from 'react-dom';
import UserNavigation from "./UserNavigation";

class Header extends Component{


    
    constructor(props) {
        super(props);
        console.log(props.state);
        this.state = "home";
        if(props.state!=null){
          this.state = props.state;
        }
        this.name = props.name;
 
      }

    render(){
        let userNavigation = new UserNavigation();

        return(
            <header className="App-header">
            <img src={logo} className="App-logo" alt="logo" />
            <div align="right">
              { (this.state=="home" || this.state=="register")?
              (<div onClick={UserNavigation.loginPage}>Login</div>):
              (<div/>)}
              {(this.state=="home" || this.state=="login")?
              (<div onClick={UserNavigation.registerPage}>Register</div>):
              (<div/>)}
              { (this.state=="user")?
             'Welcome '+this.name:(<div/>) }
            </div>
            <h1 className="App-title">{process.env.NODE_ENV}</h1>
          </header>
          );
    }
}


  
export default Header;


